/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "CoFo Sans";
  src: url("../fonts/CoFoSans-Regular.woff2") format("woff2"), url("../fonts/CoFoSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mabry Pro";
  src: url("../fonts/MabryPro-Regular.woff2") format("woff2"), url("../fonts/MabryPro-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mabry Pro";
  src: url("../fonts/MabryPro-Medium.woff2") format("woff2"), url("../fonts/MabryPro-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mabry Pro";
  src: url("../fonts/MabryPro-Bold.woff2") format("woff2"), url("../fonts/MabryPro-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  font-family: "Mabry Pro", "Arial", sans-serif;
  background-color: #FFFFFF;
  color: #373042;
  font-size: 18px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

h2 {
  font-size: 40px;
}

h2, h3 {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
  border: none;
}

p a {
  color: #7557FF;
  transition: font-weight;
}
p a:hover {
  font-weight: bold;
}

.visually-hidden {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  max-width: 1920px;
  padding: 0 20px;
  margin: 0 auto;
}

.header {
  background-color: #FFFFFF;
  position: relative;
  padding: 20px 0;
}
.header__logo {
  max-width: 180px;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #7557FF;
  width: 100%;
  padding: 20px 0 0 0;
  transition: right 0.2s;
  min-height: 100dvh;
}
.header__nav.hidden {
  right: -100%;
}
.header__link {
  display: block;
  padding: 20px;
  color: #FFFFFF;
  border-bottom: 1px solid #FFFFFF;
}
.header__menu {
  margin-bottom: 20px;
}
.header__buttons {
  padding: 0 0 20px 20px;
}
.header__button {
  display: inline-block;
  margin: 0 20px 0 0;
}

.burger {
  background-color: #7557FF;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 100%;
  cursor: pointer;
  position: relative;
}
.burger__line {
  display: block;
  width: 14px;
  height: 2px;
  background-color: #FFFFFF;
  transition: background-color 0.2s;
  position: relative;
}
.burger__line::before, .burger__line::after {
  content: "";
  display: block;
  width: 14px;
  height: 2px;
  background-color: #FFFFFF;
  transition: all 0.2s;
  position: absolute;
  left: 0;
}
.burger__line::before {
  top: -6px;
}
.burger__line::after {
  bottom: -6px;
}
.burger:hover {
  background-color: #373042;
}
.burger.active .burger__line {
  background-color: transparent;
}
.burger.active .burger__line::before {
  top: 0;
  transform: rotate(45deg);
}
.burger.active .burger__line::after {
  bottom: 0;
  transform: rotate(-45deg);
}

.button {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  padding: 20px;
  border-radius: 40px;
  min-width: 132px;
  box-sizing: border-box;
  transition: all 0.2s;
  background-color: #7557FF;
  color: #FFFFFF;
}
.button:hover {
  -webkit-box-shadow: 0px 14px 30px 0px rgba(117, 87, 255, 0.2);
  -moz-box-shadow: 0px 14px 30px 0px rgba(117, 87, 255, 0.2);
  box-shadow: 0px 14px 30px 0px rgba(117, 87, 255, 0.2);
}
.button--black {
  background-color: #373042;
  color: #FFFFFF;
}
.button--black:hover {
  background-color: #FFFFFF;
  color: #373042;
}
.button--white {
  background-color: #FFFFFF;
  color: #373042;
  border: 2px solid #7557FF;
}
.button--white:hover {
  background-color: #373042;
  color: #FFFFFF;
}

.info {
  display: flex;
  flex-direction: column-reverse;
  background-color: #373042;
  color: #FFFFFF;
}
.info__text {
  padding: 50px 20px;
}
.info__quiz {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 200px;
  padding: 6px 12px;
  background-color: rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  border-radius: 32px;
  font-size: 15px;
  font-family: "CoFo Sans", "Arial", sans-serif;
  margin-bottom: 24px;
  transition: background-color 0.2s;
}
.info__quiz:hover {
  background-color: #7557FF;
}
.info__header {
  font-size: 45px;
  font-weight: 500;
  line-height: 40px;
  margin-bottom: 20px;
}
.info__tel {
  display: block;
  font-size: 24px;
  transition: color 0.2s;
  margin-bottom: 15px;
}
.info__tel:hover {
  color: #7557FF;
}
.info__paragraph {
  margin-bottom: 24px;
}
.info__button {
  min-width: 180px;
}
.info__button:hover {
  box-shadow: none;
  background-color: #FFFFFF;
  color: #7557FF;
}

.apply {
  padding: 50px 0;
}
.apply__text {
  margin-bottom: 60px;
}
.apply__header {
  margin-bottom: 20px;
}
.apply__paragraph {
  margin-bottom: 20px;
  font-family: "CoFo Sans", "Arial", sans-serif;
}
.apply__paragraph:last-of-type {
  margin-bottom: 40px;
}
.apply__list-item {
  margin-bottom: 30px;
  border-radius: 20px;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
}
.apply__list-image {
  width: 100%;
}
.apply__list-wrapper {
  background-color: #FFFFFF;
  padding: 25px 20px;
}
.apply__list-answer, .apply__list-header, .apply__list-tel {
  display: block;
  margin-bottom: 8px;
}
.apply__list-answer {
  color: #9F9F9F;
  font-size: 17px;
  line-height: 24px;
  font-family: "CoFo Sans", "Arial", sans-serif;
  transition: color 0.2s;
}
.apply__list-answer:hover {
  color: #7557FF;
}
.apply__list-header {
  font-size: 24px;
  line-height: 1;
}
.apply__list-tel {
  cursor: pointer;
  line-height: 30px;
  transition: color 0.2s;
}
.apply__list-tel:hover {
  color: #7557FF;
}

.link {
  position: relative;
  color: #7557FF;
  font-family: "CoFo Sans", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  transition: color 0.2s;
}
.link:hover {
  font-weight: bold;
}
.link::after, .link::before {
  content: "";
  position: absolute;
  display: block;
  top: 50%;
  transition: all 0.2s;
}
.link::after {
  background-color: #7557FF;
  width: 20px;
  height: 2px;
  right: -30px;
}
.link::before {
  width: 8px;
  height: 8px;
  border: 2px solid #7557FF;
  border-left: none;
  border-bottom: none;
  right: -30px;
  transform: rotate(45deg);
  top: 35%;
}
.link:hover::after {
  right: -35px;
}
.link:hover::before {
  right: -35px;
}

.swiper-pagination-bullet {
  background: #7557FF;
  opacity: 1;
  transition: all 0.6s;
  width: 5px;
  height: 5px;
  border-radius: 5px;
}

.swiper-pagination-bullet-active {
  min-width: 23px;
}

.swiper-button-prev,
.swiper-button-next {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border-radius: 100%;
  background-color: #FFFFFF;
  top: 68%;
  -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
}
.swiper-button-prev span,
.swiper-button-next span {
  display: block;
  width: 14px;
  height: 2px;
  background-color: #373042;
  position: relative;
}
.swiper-button-prev span::after,
.swiper-button-next span::after {
  content: "";
  position: absolute;
  display: block;
  width: 6px;
  height: 6px;
  border: 2px solid #373042;
  transform: rotate(45deg);
  top: -3px;
}
.swiper-button-prev:hover, .swiper-button-prev:focus,
.swiper-button-next:hover,
.swiper-button-next:focus {
  outline: 2px solid #7557FF;
}

.swiper-button-prev span::after {
  border-top: none;
  border-right: none;
  left: 0;
}

.swiper-button-next span::after {
  border-left: none;
  border-bottom: none;
  right: 0;
}

.swiper-button-prev {
  left: 0;
}

.swiper-button-next {
  right: 0;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}